/*input range chrome and safary*/
input[type="range"] {
    --range-progress: 0;

    --webkit-apparence: none;
    position: relative;
    backgroud: #ccc;
    width: 100%;
    height: 2px;
    border-radius: 2px;
    cursor: pointer;
}

/*Input range -firefox */
input[type="range"]::-moz-range-track{
    position: relative;
    background: #ccc;
    width: 100%;
    height: 2px;
    border-radius: 2px;
    cursor: pointer;
}

/*played progress length -chrome & safary*/
input[type="range"]::before {
    content: '';
    height: 2px;
    background: rgb(114, 240, 103);
    width: var(--range-progress);
    border-bottom-left-radius: 2px;
    border-bottom-left-radius: 2px;
    position: absolute;
    top: 0;
    left: 0;
}

/*played progress length - firefox*/
input[type="range"]::-moz-range-progress{
    background: #f50;
    border-bottom-left-radius: 2px;
    border-top-left-radius: 2px;
    height: 2px;
}

/*slider thumb -chrome and safary*/
input[type="range"]::-webkit-slider-thumb {
    -webkit-appearance: none;
    height: 8px;
    width: 8px;
    border-radius: 50%;
    border: none;
    background-color: rgb(68, 199, 28);
    cursor: pointer;
    position: relative;
}

/*draging thumb -chrome and safary*/
input[type="range"]::-webkit-slider-thumb{
    transform: scale(1.2);
}

/*slider thumb -firefox*/
input[type="range"]::-moz-range-thumb{
    height: 8px;
    width: 8px;
    border-radius: 50%;
    background: rgb(19, 179, 80);
    cursor: pointer;
    border: transparent;
    position: relative;
}

/*dragging thumb -firefox */
input[type="range"]:active::-moz-range-thumb{
    transform: scale(1.2);
}
